* {
    margin: 0;
    padding: 0;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
.login {
    display: grid;
    place-items: center;
    height: 100vh;
    background-color: black;
}

.login>img {
    width: auto;
    height: 200px;
}

.login>a {
    padding: 20px;
    border-radius: 99px;
    font-weight: 600;
    color: white;
    text-decoration: none;
    background-color: #1db954;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.player__body {
    display: flex;
}
.body {
    height: 100vh;
    flex: 0.8 1;
    color: white;
    padding: 30px;
    background: linear-gradient(rgb(91, 87, 115), rgba(0, 0, 0, 1));
    overflow-y: overlay;
}

.body::-webkit-scrollbar {
    display: none;
}

.body__info {
    display: flex;
    align-items: flex-end;
    padding: 10px;
}

.body__infoText {
    flex: 1 1;
}

.body__infoText>h2 {
    font-size: 48px;
    margin-bottom: 10px;
}

.body__infoText>p {
    font-size: 14px;
}

.body__info>img {
    height: 20vw;
    margin: 0 20px;
    box-shadow: 0 4px 60px rgba(0, 0, 0, 0.5);
}

.body__icons {
    display: flex;
    align-items: center;
}

.body__icons>.MuiSvgIcon-root {
    margin-right: 20px;
}

.body__shuffle {
    font-size: 80px !important;
    margin-left: 50px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.body__shuffle:hover {
    transition: 100ms -webkit-transform ease-in;
    transition: 100ms transform ease-in;
    transition: 100ms transform ease-in, 100ms -webkit-transform ease-in;
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
}

.body__songs {
    margin: 20px -30px;
}
* {
    margin: 0;
}

body::-webkit-scrollbar {
    display: none;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
.songRow {
    margin-left: 20px;
    padding: 20px;
    display: flex;
    align-items: center;
    z-index: 100;
    color: white;
}

.songRow:hover {
    cursor: pointer;
    background-color: black;
    opacity: 0.8;
}

.songRow__info {
    margin-left: 20px;
}

.songRow__info>h1 {
    font-size: 16px;
}

.songRow__info>p {
    font-size: 14px;
    margin-top: 3px;
    color: gray;
}

.songRow__album {
    height: 40px;
    width: 40px;
}
.footer {
    position: fixed;
    display: flex;
    justify-content: space-evenly;
    bottom: 0;
    height: 65px;
    width: 100%;
    background-color: #282828;
    padding: 20px;
}

.footer__left {
    flex: 0.3 1;
    display: flex;
    align-items: center;
    color: white;
    max-width: 300px;
}

.footer__center {
    flex: 0.4 1;
    padding: 0 100px;
    color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 300px;
}

.footer__right {
    flex: 0.3 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: white;
}

.footer__albumLogo {
    height: 60px;
    width: 60px;
    margin-right: 20px;
    object-fit: contain;
}

.footer__green {
    color: #1ed15e;
}

.footer__songInfo>h4 {
    margin-bottom: 5px;
}

.footer__songInfo>p {
    font-size: 12px;
}

.footer__icon:hover,
.footer__green:hover {
    transition: -webkit-transform 0.2 ease-in;
    transition: transform 0.2 ease-in;
    transition: transform 0.2 ease-in, -webkit-transform 0.2 ease-in;
    -webkit-transform: scale(1.2) !important;
            transform: scale(1.2) !important;
}

.footer__right * .MuiSlider-root {
    color: #1ed15e;
}
.sidebarOption {
    display: flex;
    align-items: center;
    color: gray;
    height: 40px;
    cursor: pointer;
    transition: 200ms color ease-in;
}

.sidebarOption:hover {
    color: white;
}

.sidebarOption__icon {
    padding-left: 10px;
    padding-right: 10px;
}

.sidebarOption>p {
    margin-top: 10px;
    margin-left: 20px;
    font-size: 14px;
}
.sidebar {
    height: 100vh;
    color: white;
    padding-left: 10px;
    padding-right: 10px;
    flex: 0.2 1;
    min-width: 230px;
    background-color: #040404;
}

.sidebar>hr {
    border: 1px solid #282828;
    width: 90%;
    margin: 10px auto;
}

.sidebar__logo {
    height: 100px;
    width: auto;
    padding: 10px;
    margin: auto;
}

.sidebar__title {
    margin-left: 5px;
    padding: 5px;
    font-size: 12px;
}
